.heading2,
.heading3 {
  display: flex;
  justify-content: center;
  align-content: center;
  padding: 12px;
  color: rgb(19, 1, 1);
  background-color: cornsilk;
  border: wheat 3px solid;
}

body {
  background-color: rgba(216, 36, 126, 0.163);
  display: flex;
  flex-direction: column;
}
.card-text {
  font-size: 30rem;
  display: flex;
  color: black;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}
.column {
  display: flex;
}
.container {
  display: flex;
  justify-content: center;
  flex: 3;
  flex-direction: column;
}
.card1 {
  height: 339px;
  width: 20rem;
  margin-bottom: 20px;
  color: black;
}
.card1:hover {
  background-color: rgb(228, 168, 238);
  width: 25rem;
  border-color: white;
}
.card {
  justify-content: center;
}
